import React, { Component } from 'react'
import StateType from "@/components/stateType/"
import TopArr from "@/components/topNav/"
import TableBox from "@/components/table/index.jsx"
import "./style/fulfill.css"
import Bread from "@/components/Bread/"


class Fulfill extends Component {
    state = {
        navArr: ["上报项一", "上报项二", "上报项三", "上报项四", "上报项五"],
        topArr: ["导出","待审核", "审核完成"]
    }
    render() {
        let { navArr, topArr } = this.state;
        return (
            <div className="fulfill-box">
                <Bread history={this.props.history}/>
                <TopArr navArr={navArr} switchFn={this.switchFn} />
                <StateType topArr={topArr} />
                <div className="content">
                    <TableBox />
                </div>
            </div>
        )
    }
    switchFn(index){
        console.log(index)
    }
}


export default Fulfill;